* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

::-webkit-scrollbar {
  width: 0;
}

body {
  width: 100%;
  height: 100vh;
  background-color: var(--background-color);

  --primary-color: #255fff;
  --background-color: #255fff;
  --delete-color: #ff0e0e;
}

#root {
  width: 100%;
  height: auto;
  min-height: 100vh;
  display: grid;
  place-items: center;
}



.btn {
  font-size: 0.8rem;
  padding: 6px 13px;
  cursor: pointer;
  background-color: var(--btn-color);
  border: 2px solid var(--btn-color);
  border-radius: 9999px;
  color: white;
  font-weight:  700;
  transition: 0.3s;
}

.btn-curve-left, .btn-curve-right {
  padding: 9px 15px;
  border: 0;
}

.btn-curve-right {
  padding-left: 10px;
  border-left: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}

.btn-curve-left {
  padding-right: 10px;
  border-right: 0;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.group {
  /* padding: 9px 19px; */
  background-color: var(--btn-color);
  display: flex; 
  /* gap: 10px; */
  border-radius: 9999px;
}

.btn:hover {
  background-color: white;
  color: var(--btn-color);
}

.btn.warning {
  --btn-color: var(--delete-color);
}

.todo {
  --inner-color: #e4e4e4ee;
  --popup-color: #f3f3f3;
  --disable-color: #aaaaaaa8;
  --btn-color: #0084ff;

  margin: 130px 0;
  width: 90%;
  max-width: 550px;
  border-radius: 30px;
  background-color: var(--inner-color);
  box-shadow: 0 0 60px -30px black;
  padding: 30px;
  font-family: sans-serif;
}



.popup {
  position: relative;
}

.popup .btn {
  width: 100%;
  height: 100%;
}

.popup-outer {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: transparent;
  z-index: 3;
}

.appear .popup-outer {
  display: block;
}

.popup-group {
  position: absolute;
  top: calc(100% + 15px);
  right: 0px;
  width: 200px;
  z-index: 3;
  display: none;
}

.popup-group-content {
  z-index: 4;
  display: block;
  position: relative;
  padding: 20px 30px;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  top: 0px;
  right: 0px;
  display: flex;
  flex-direction: column;
  grid-gap: 13px;
  gap: 13px;
  justify-content: space-between;
  background-color: var(--popup-color);
  box-shadow: 0 0 60px -30px black;
  -webkit-animation: appear 0.3s ease-out;
          animation: appear 0.3s ease-out;
  color: black;
}

.popup-group-content::before {
  content: "";
  position: absolute;
  top: -4px;
  right: 20px;
  width: 16px;
  height: 16px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  background-color: var(--popup-color);
} 

.appear {
  display: flex;
  -webkit-animation: appear 0.2s ease-out;
          animation: appear 0.2s ease-out;
}

.disappear {
  display: flex;
  -webkit-animation: disappear 0.2s ease-in forwards;
          animation: disappear 0.2s ease-in forwards;
}

@-webkit-keyframes appear {
  from {
    opacity: 0;
    -webkit-transform: translate(0, -20px);
            transform: translate(0, -20px);
  }
  to {
    opacity: 1;
    -webkit-transform: translate(0, 0px);
            transform: translate(0, 0px);
  }
}

@keyframes appear {
  from {
    opacity: 0;
    -webkit-transform: translate(0, -20px);
            transform: translate(0, -20px);
  }
  to {
    opacity: 1;
    -webkit-transform: translate(0, 0px);
            transform: translate(0, 0px);
  }
}

@-webkit-keyframes disappear {
  from {
    opacity: 1;
    visibility: visible;
    -webkit-transform: translate(0, 0px);
            transform: translate(0, 0px);
  }
  to {
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translate(0, 20px);
            transform: translate(0, 20px);
  }
}

@keyframes disappear {
  from {
    opacity: 1;
    visibility: visible;
    -webkit-transform: translate(0, 0px);
            transform: translate(0, 0px);
  }
  to {
    opacity: 0;
    visibility: hidden;
    -webkit-transform: translate(0, 20px);
            transform: translate(0, 20px);
  }
}

.todo h1 {
  margin-bottom: 10px;
}

.todo .header {
  display: flex;
  position: relative;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.todo .function-filter {
  display: flex;
  flex-direction: column;
}

.todo .function-filter select {
  padding: 5px;
  padding-left: 15px;
  -webkit-appearance: none;
          appearance: none;
  background-color: white;
  border-radius: 10px;
}

.todo .function-filter select:focus {
  outline: none;
}

.todo hr {
  height: 1px;
  border: none;
  background-color: #b4b4b4d7;
}

.todo .checkbox {
  display: flex;
  grid-gap: 5px;
  gap: 5px;
  align-items: center;
}



.todo .input-area { 
  display: grid;
  grid-gap: 10px;
  gap: 10px;
  grid-template-columns: 5fr 1fr 1fr;
  height: 40px;
  margin-top: 20px;
}

.todo .input-area .task-input {
  border: none; 
  background-color: transparent;
  font-size: 1rem;
  padding-bottom: 0px;
}

.todo .input-area .task-input:focus {
  border: none;
  outline: none;
  border-bottom: 3px solid var(--primary-color);
}

.todo form.edit-form {
  display: flex;
  flex-direction: column;
  grid-gap: 15px;
  gap: 15px;
}

.todo .edit-input {
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
}

.todo .edit-input .title {
  color: #868686;
  font-size: 0.8rem;
  font-weight: bold;
}

textarea {
  font-family: sans-serif;
  font-size: 0.8rem;
  letter-spacing: 1px;
}

.todo .edit-input > input, .todo .edit-input > textarea {
  padding: 9px 10px;
  border: 1px solid var(--disable-color);
  border-radius: 15px;
  resize: none;
  -webkit-appearance: none;
          appearance: none;
}

.todo .edit-input > *:focus {
  outline: none;
}





@media (max-width: 550px) {
  .todo .input-area {
    grid-template-columns: 1fr 1fr !important;
    grid-template-rows: 40px 40px;
    height: 90px;
    grid-template-areas: 
                      "a a"
                      "b c";
  }
  .input-area .task-input {
    grid-area: a;
  }
  .input-area > .popup {
    grid-area: b;
  }
  .input-area > button {
    grid-area: c;
  }
  .input-area > .popup .popup-group {
    --position: -10px;
    left: var(--position) ;
  }
  .input-area > .popup .popup-group-content::before {
    left: calc(80px - var(--position)) ;
  }
}

.todo .task-area {
  margin-top: 35px;
}

.todo .task-area .task-list {
  list-style-type: none;
}

.todo .task-area .task-list .task-list-item {
  display: grid; 
  grid-template-columns: 3fr 1fr;
  justify-content: space-between;
  margin: 14px 0; 
  padding: 15px;
  padding-left: 20px;
  color: white;
  background-color: var(--primary-color);
  border-radius: 10px;
  transition: 0.2s;
  box-shadow: 0 0 20px -10px black;
  position: relative;
}

.popup-group {
  --position: -150px;
  left: var(--position);
}

.popup-group-content::before {
  left: calc(20px - var(--position));
}


.todo .task-area .task-list .task-list-item .left-side .popup-group {
  --position: -150px;
  left: var(--position);
}

.todo .task-area .task-list .task-list-item .left-side .popup-group-content::before {
  left: calc(20px - var(--position));
}

.todo .task-area .task-list .task-list-item.disable {
  --btn-color: var(--disable-color) !important;
  background-color: var(--disable-color);
}

.todo .task-area .task-list .task-list-item .more-icon {
  fill: white !important;
}

.todo .task-area .task-list .task-list-item .more-icon > *:hover {
  cursor: pointer;
}

.todo .task-list-item > .left-side {
  font-weight: bold;
  display: flex;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.task-title {
  max-width: 90%;
}

.todo .task-list-item > .left-side {
}

.todo .task-date {
  color: #dadada;
  font-size: 0.85rem;
  margin-left: 10px;
  font-style: italic;
  font-weight: 500;
}

.todo .task-list-item .more-icon {
  display: flex;
  align-items: center;
  padding-left: 35px;
}

.todo .task-list-item .more-icon > * {
  margin: 0 3px;
}

.todo .task-list-item .more-icon * {
  font-size: 0.9rem;
}

.todo .task-list-item .task-description {
  color: black;
  font-family: 'Patrick Hand', cursive;
  font-size: 1.1rem;
}

.fas, .far {
  transition: 0.2s;
}

.fas.fa-star {
  color: yellow;
}

div.star-icon:hover {
  background-color: yellowgreen;
  color: yellow;
}

div.trash-icon:hover {
  background-color: var(--delete-color);
  color: white;
}


/* -------------------------------------------------------------------------------------- */

@media (max-width: 550px) {
  .todo .task-area .task-list .task-list-item {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  .todo .task-area .task-list .task-list-item .more-icon {
    margin-top: 10px;
    align-self: flex-end;
  }
  .todo .task-area .task-list .task-list-item .left-side {
    justify-content: space-between;
  }
  
  .todo .task-area .task-list .task-list-item .left-side .popup-group {
    --position: -260px;
  }
}

